import React, { useEffect, useRef } from "react";
import anime from "animejs";
import { variantStyles, animations, dimensions, effects } from "./styles";

const SmokeEffect = ({ variant = "default" }) => {
  const smokeRef = useRef(null);

  useEffect(() => {
    const particles = Array.from({ length: 15 }).map(() => {
      const particle = document.createElement("div");
      particle.className = "smoke-particle";
      smokeRef.current?.appendChild(particle);
      return particle;
    });

    const animations = particles.map(particle => {
      return anime({
        targets: particle,
        translateY: [60, -100],
        translateX: () => anime.random(-50, 50),
        scale: [0.5, 3],
        opacity: [
          { value: 0, duration: 0 },
          { value: 0.5, duration: 200 },
          { value: 0, duration: 800 }
        ],
        rotate: () => anime.random(-360, 360),
        duration: () => anime.random(2000, 3000),
        easing: 'easeOutExpo',
        loop: true,
        delay: () => anime.random(0, 2000)
      });
    });

    return () => animations.forEach(anim => anim.pause());
  }, []);

  return (
    <div className="smoke-container">
      <div ref={smokeRef} className="particles" />
      <style>
        {`
          .smoke-container {
            position: absolute;
            inset: 0;
            overflow: hidden;
            pointer-events: none;
            z-index: 0;
          }

          .smoke-particle {
            position: absolute;
            bottom: 0;
            left: 50%;
            width: 20px;
            height: 20px;
            background: ${variantStyles.smoke.particleColor};
            border-radius: 50%;
            filter: blur(8px);
          }

          .smoke-container::before {
            content: '';
            position: absolute;
            inset: 0;
            background: radial-gradient(
              circle at center,
              transparent 30%,
              rgba(0, 0, 0, 0.2) 60%
            );
            z-index: 1;
          }
        `}
      </style>
    </div>
  );
};

const RevealText = ({ text, color = "#fff", delay = 0 }) => {
  return (
    <div className="reveal-text">
      {text.split('').map((char, i) => (
        <span
          key={i}
          style={{
            display: 'inline-block',
            opacity: 0,
            animation: 'reveal 1s forwards',
            animationDelay: `${delay + (i * 0.1)}s`,
            color,
          }}
        >
          {char}
        </span>
      ))}
      <style>
        {`
          @keyframes reveal {
            0% {
              opacity: 0;
              transform: rotateY(90deg);
              filter: blur(10px);
            }
            100% {
              opacity: 1;
              transform: rotateY(0);
              filter: blur(0);
            }
          }
        `}
      </style>
    </div>
  );
};

export default function SmokeToast({
  message,
  description,
  action,
  variant = "default",
  onClose,
  style = {},
  ...rest
}) {
  const toastRef = useRef(null);
  const progressRef = useRef(null);

  useEffect(() => {
    anime({
      targets: toastRef.current,
      translateY: [20, 0],
      opacity: [0, 1],
      duration: animations.slideIn.duration,
      easing: animations.slideIn.easing
    });

    if (progressRef.current) {
      anime({
        targets: progressRef.current,
        width: ["0%", "100%"],
        duration: animations.progressBar.duration,
        easing: animations.progressBar.easing
      });
    }
  }, []);

  return (
    <div
      ref={toastRef}
      style={{
        display: "flex",
        alignItems: "center",
        width: dimensions.width,
        maxWidth: dimensions.maxWidth,
        minHeight: dimensions.minHeight,
        padding: dimensions.padding,
        margin: dimensions.margin,
        borderRadius: dimensions.borderRadius,
        background: "linear-gradient(135deg, rgba(0,0,0,0.9), rgba(20,20,20,0.95))",
        backdropFilter: `blur(${effects.blur.medium})`,
        color: "#FFFFFF",
        boxShadow: effects.shadow.lg,
        border: "1px solid rgba(255, 255, 255, 0.1)",
        position: "relative",
        overflow: "hidden",
        ...style
      }}
      {...rest}
    >
      <SmokeEffect variant={variant} />
      <div
        style={{
          position: "absolute",
          bottom: 0,
          left: 0,
          right: 0,
          height: "3px",
          background: "rgba(255, 255, 255, 0.1)"
        }}
      >
        <div
          ref={progressRef}
          style={{
            height: "100%",
            background: "rgba(255, 255, 255, 0.3)",
            width: "0%"
          }}
        />
      </div>
      <div
        style={{
          flex: 1,
          position: "relative",
          zIndex: 1
        }}
      >
        <RevealText 
          text={message}
          color={variantStyles.smoke.text[variant]}
        />
        {description && (
          <RevealText 
            text={description}
            color="rgba(255, 255, 255, 0.8)"
            delay={0.5}
          />
        )}
      </div>
      {action && (
        <button
          onClick={action.onClick}
          style={{
            background: "rgba(255, 255, 255, 0.1)",
            border: "1px solid rgba(255, 255, 255, 0.2)",
            color: "#fff",
            padding: "8px 16px",
            marginLeft: dimensions.spacing.buttons,
            borderRadius: "6px",
            cursor: "pointer",
            fontSize: "14px",
            fontWeight: 500,
            position: "relative",
            zIndex: 1,
            transition: "all 0.3s ease",
            backdropFilter: `blur(${effects.blur.light})`
          }}
          onMouseEnter={(e) => {
            e.target.style.background = "rgba(255, 255, 255, 0.2)";
            e.target.style.transform = "translateY(-1px)";
          }}
          onMouseLeave={(e) => {
            e.target.style.background = "rgba(255, 255, 255, 0.1)";
            e.target.style.transform = "translateY(0)";
          }}
        >
          {action.label}
        </button>
      )}
      <button
        onClick={onClose}
        style={{
          background: "transparent",
          border: "none",
          color: "#fff",
          marginLeft: dimensions.spacing.buttons,
          cursor: "pointer",
          fontSize: "18px",
          opacity: 0.7,
          position: "relative",
          zIndex: 1,
          transition: "opacity 0.2s"
        }}
        onMouseEnter={(e) => e.target.style.opacity = "1"}
        onMouseLeave={(e) => e.target.style.opacity = "0.7"}
      >
        ✕
      </button>
    </div>
  );
}
